import React, { useEffect,useState } from "react";
import { MdInfoOutline } from "react-icons/md";
import RightDrawer from "../RightDrawer/RightDrawer";
import Tooltip from '@mui/material/Tooltip';
import CustomTooltip from "../Tooltip/CustomTooltip";
import RelatedOperationsMenu from "../RelatedOperation/RelatedOperation";

export default function Tabs({
  tab1name,
  isRecordTab,
  tab2name,
  setMode,
  setIsRecordTab,
  mode,
  setResetState,
  resetState,
  handleEditClick,
  articleModel = [],
  videoModel = [],
  showYouTubeVideo = true,
  showRelatedOperation = true,
  menuItems = [],
  tooltipText = ""
}) {
    const [isDrawerOpen, setIsDrawerOpen] = useState(false)
    // Add/remove body class to prevent scrolling when drawer is open
  useEffect(() => {
    if (isDrawerOpen) {
      document.body.classList.add('drawer-open');
    } else {
      document.body.classList.remove('drawer-open');
    }
    
    // Cleanup on unmount
    return () => {
      document.body.classList.remove('drawer-open');
    };
  }, [isDrawerOpen]);
    const handleDrawerToggle = () => {
        setIsDrawerOpen(!isDrawerOpen);
      };
    
      const handleDrawerClose = () => {
        setIsDrawerOpen(false);
      };      
  return (
    <>
      <ul
        className="nav nav-tabs col-md-11 col-sm-11"
        id="myTab"
        role="tablist"
      >
        <li className="nav-item">
          <a
            className={`nav-link ${isRecordTab ? "tabactive active" : ""}`}
            data-toggle="tab"
            href="#tabs-2"
            role="tab"
            id="recordsTab"
            onClick={() => {
              setIsRecordTab(true);
              setMode("");
            }}
          >
            {tab1name || "Record"} 
            {
              tooltipText != "" && <span style={{marginLeft:'8px'}}>
              <CustomTooltip
                          placement="top"
                          body={`${tooltipText}`}
                        >
                          <MdInfoOutline size={20} />
                        </CustomTooltip>
            </span>
            }            
          </a>
        </li>
        <li className="nav-item">
          <a
            className={`nav-link ${!isRecordTab ? "tabactive active" : ""}`}
            data-toggle="tab"
            href="#tabs-1"
            role="tab"
            id="grn"
            onClick={() => {
              setIsRecordTab(false);
              setMode("new");
            }}
          >
            {tab2name || "Defintion"}
          </a>
        </li>
        {!isRecordTab && (
          <li
            className={`${
              mode.toLowerCase() == "viewmode" ? "viewModeLabel" : "modeLabel"
            }`}
            style={{
              color:
                mode.toLowerCase() == "new"
                  ? "rgb(39, 174, 96)"
                  : mode.toLowerCase() == "viewmode" ||
                    mode.toLowerCase() == "poapproved"
                  ? "red"
                  : "rgb(247, 148, 29)",
            }}
          >
            {mode.toLowerCase() == "new"
              ? "Add New Record"
              : mode.toLowerCase() == "viewmode"
              ? "View mode only"
              : mode.toLowerCase() == "poapproved"
              ? "Approved PO cannot be Updated"
              : "Edit Existing Record"}
          </li>
        )}
        <li className="modeBtnContainer">
          <CustomTooltip body="Create New Record">
          <button
            style={{
              borderRadius: isRecordTab
                ? "30px 0px 0px 30px"
                : "30px 30px 30px 30px",
            }}
            className="page_add_btn"
            onClick={() => {
              setMode("new");
              setResetState(!resetState);
              setIsRecordTab(false);
            }}
          >
            <img
              src="data:image/png;base64,"
              width="15"
              height="15"
              alt=""
            />
            New
          </button>
          </CustomTooltip>
          {isRecordTab && (
            <CustomTooltip body="Modify Selected Record">
            <button
              className="page_edit_btn"
              onClick={() => {
                handleEditClick();
              }}
            >
              <img
                src="data:image/png;base64,"
                width="15"
                height="15"
                alt=""
              />
              Edit
            </button>
            </CustomTooltip>
          )}
          {
            showRelatedOperation && <RelatedOperationsMenu menuItems={menuItems} />
          }
          {
            showYouTubeVideo && 
            <Tooltip title="Videos and Help Documents" placement="top">
            <span className="yotubeIcon" onClick={handleDrawerToggle}>
            <span className="youTubeText">Learn</span>
            <img
              src="data:image/png;base64,"
              alt="youTube"
            />
          </span>
          </Tooltip>
          }          
        </li>
      </ul>
      

      {/* Right Drawer */}
      <RightDrawer
        isOpen={isDrawerOpen}
        onClose={handleDrawerClose}
        videoModel={videoModel}
        articleModel={articleModel}
        setIsOpen={setIsDrawerOpen}
      ></RightDrawer>
    </>
  );
}
